import React, { Fragment, useEffect, useState } from "react";
import axios from "axios";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import {
  MdArrowBack,
  MdBrightness1,
  MdDelete,
  MdRestoreFromTrash,
  MdEdit,
  MdExpandLess,
  MdExpandMore,
} from "react-icons/md";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import { deletePosition, getPositions } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import { education_category } from "../../interfaces/education_category";
import { school_level } from "../../interfaces/school_level";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PositionAPI, PositionRequirements } from "../../interfaces/position";
import { StoreState } from "../../reducers";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import ViewPosition from "./ViewPosition";
import { course } from "../../interfaces/course";
import { DocumentFieldsInterface } from "../../shared/interfaces";
import PositionForm from "./PositionForm";
import searchData from "../../utils/search";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import { RiFolderSettingsFill } from "react-icons/ri";
import { useParams } from "react-router-dom";
import ActionConfirm, {
  ActionConfirmEnum,
} from "../../components/ActionConfirm/ActionConfirm";
import { isEmptyOrSpaces } from "../../shared/dataTest";

export interface positionRequirementInterface {
  category_subcategory_data_id: string;
  required: boolean;
}

// type FilterByPublishedStatusTypes = {
//   all: "ALL";
//   published: "PUBLISHED";
//   not_published: "NOT_PUBLISHED";
// };

interface AppPropsInterface {
  systemPositions: PositionAPI[] | null;
  getPositions: Function;
  deletePosition: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Index = (props: AppPropsInterface) => {
  const [deleteAction, setDeleteAction] = useState<string>("");
  const { view_type } = useParams<{ view_type: string }>();
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [isDeleting, setIsDeleting] = useState<string>("");
  const [thisPosition, setThisPosition] = useState<PositionAPI | null>(null);
  const [modalAction, setModalAction] = useState<
    "CREATE_POSITION" | "UPDATE_POSITION" | false
  >(view_type === "create" ? "CREATE_POSITION" : false);
  const [formSteps, setFormSteps] = useState<{
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
    FOUR: boolean;
  }>(
    view_type === "create"
      ? { ONE: true, TWO: false, THREE: false, FOUR: false }
      : { ONE: false, TWO: false, THREE: false, FOUR: false }
  );
  const [thisEdication, setThisEdication] = useState<education_category | null>(
    null
  );
  const [thisLevel, setThisLevel] = useState<school_level | null>(null);
  const [thisCourses, setThisCourses] = useState<course[] | null>(null);
  const [thisCoursesId, setThisCoursesId] = useState<string[] | null>(null);
  const [positionId, setPositionId] = useState<string>("");
  const [positionTitle, setPositionTitle] = useState<string>("");
  const [positionCode, setPositionCode] = useState<string>("");
  const [positionDesc, setPositionDesc] = useState<string>("");
  const [thisCategory, setThisCategory] = useState<string>("");
  const [thisSubCategory, setThisSubCategory] = useState<string>("");
  const [thisFieldList, setThisFieldList] = useState<
    positionRequirementInterface[] | null
  >(null);
  const [thisRequirementList, setThisRequirementList] = useState<
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null
  >(null);
  const [stepState, setStepState] = useState<{
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
    FOUR: boolean;
  }>({ ONE: false, TWO: false, THREE: false, FOUR: false });

  // FOR STEP THREE
  const [filteredFields, setFilteredFields] = useState<
    DocumentFieldsInterface[] | null
  >(null);
  const [thisField, setThisField] = useState<string>("");
  const [isFieldRequired, setIsFieldRequired] = useState<boolean>(false);
  const [isRequirementRequired, setIsRequirementRequired] =
    useState<boolean>(false);
  const [courseLength, setCourseLength] = useState<number>(0);
  const [chooseFields, setChooseFields] = useState<boolean>(false);
  const [searchPosition, setSearchPosition] = useState<string>("");
  const [filterByPublishedStatus, setFilterByPublishedStatus] = useState<
    "ALL" | "PUBLISHED" | "NOT_PUBLISHED"
  >("ALL");
  const [filterByPositionStatus, setFilterByPositionStatus] = useState<
    "TRUE" | "FALSE"
  >("TRUE");
  const [positionSubCategoryId, setPositionSubCategoryId] =
    useState<string>("");

  useEffect(() => {
    if (props.systemPositions === null) {
      props.getPositions((status: boolean, msg: string) => {
        setLoading(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (loading) {
      setLoading(false);
    }
  }, [loading, props, setLoading]);

  const handleDelete = async (position_id: string) => {
    // if (window.confirm("Are you sure do you want to delete this position?")) {
    setIsDeleting(position_id);
    setLoadingRequest(true);
    try {
      const res = await axios.delete(
        `${API.APPLICATION}/position/${position_id}`
      );

      if (res.data) {
        console.log({ res });
        props.deletePosition(position_id);
        props.PushPopupAlert({
          title: "Position deleted successfully",
          theme: PopupAlertTheme.success,
        });
      }
    } catch (error) {
      console.log({ position_error: error });
      props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setIsDeleting("");
    setLoadingRequest(false);
    // }
  };

  const cleanStates = () => {
    setChooseFields(false);
    setThisEdication(null);
    setThisLevel(null);
    setThisCourses(null);
    setThisCoursesId(null);
    setThisCategory("");
    setThisSubCategory("");
    setPositionTitle("");
    setPositionCode("");
    setPositionId("");
    setPositionSubCategoryId("");
    setPositionDesc("");
    setThisFieldList(null);
    setThisRequirementList(null);

    setFilteredFields(null);
    setThisField("");
    setIsFieldRequired(false);
    setIsRequirementRequired(false);
    setCourseLength(0);
    setFormSteps({ ONE: true, TWO: false, THREE: false, FOUR: false });
    setStepState({ ONE: true, TWO: false, THREE: false, FOUR: false });
  };

  const assignRequirementsUpdate = (requirements: PositionRequirements[]) => {
    console.log({ requirements });
    let newArray: {
      user_detail_category_id: string;
      user_detail_subcategory_id: string;
      position_requirement: positionRequirementInterface[];
      required: boolean;
    }[] = [];
    let newRequirement: positionRequirementInterface[] = [];
    // let
    requirements.forEach((element) => {
      console.log({ element });
      element.position_requirement.forEach((item) => {
        console.log({ item });
        newRequirement.push({
          category_subcategory_data_id: item._id,
          required: item.required,
        });
      });
      newArray.push({
        user_detail_category_id: element.category.user_detail_category_id,
        user_detail_subcategory_id:
          element.subcategory.user_detail_subcategory_id,
        position_requirement: newRequirement,
        required: element.required,
      });
      newRequirement = [];
    });

    return newArray;
  };

  const styelingController = (
    position: PositionAPI
  ): { section: string; button: string } => {
    let sectionStyles: string = "",
      btnStyles: string = "bg-blue-100 group-hover:bg-blue-50";
    if (thisPosition) {
      if (thisPosition.position_id === position.position_id) {
        if (thisPosition.isPublished) {
          sectionStyles = "bg-blue-100 border border-blue-500";
          btnStyles = "bg-blue-100 group-hover:bg-blue-50";
        } else {
          sectionStyles = "bg-yellow-100 border border-yellow-500";
          btnStyles = "bg-yellow-100 group-hover:bg-yellow-50";
        }
      }
    }
    return { section: sectionStyles, button: btnStyles };
  };

  const filterByPublishement = (
    positions: PositionAPI[],
    filterValue: "ALL" | "PUBLISHED" | "NOT_PUBLISHED"
  ): PositionAPI[] => {
    let result: PositionAPI[] = [];
    if (filterValue === "ALL") {
      result = positions;
    } else if (filterValue === "PUBLISHED") {
      result = positions.filter((item) => item.isPublished);
    } else if (filterValue === "NOT_PUBLISHED") {
      result = positions.filter((item) => !item.isPublished);
    }

    return result;
  };

  const filterByStatus = (
    positions: PositionAPI[],
    filterValue: "ALL" | "TRUE" | "FALSE"
  ): PositionAPI[] => {
    let result: PositionAPI[] = [];
    if (filterValue === "ALL") {
      result = positions;
    } else if (filterValue === "TRUE") {
      result = positions.filter((item) => !item.isDeleted);
    } else if (filterValue === "FALSE") {
      result = positions.filter((item) => item.isDeleted);
    }

    return result;
  };

  if (props.systemPositions === null || loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading...
      </div>
    );
  }

  return (
    <Fragment>
      <div className="mt-3 rounded flex flex-row justify-between items-center">
        <div className="flex flex-row items-center gap-3">
          <RiFolderSettingsFill className="text-6xl text-blue-600" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-2xl font-bold">Positions</span>
          </div>
        </div>
      </div>
      <div className="mt-1 p-1 bg-white">
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          <section className="flex items-center gap-2">
            {modalAction && (
              <button
                className={`ml-3 bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                  loadingRequest ? "cursor-not-allowed" : "cursor-pointer"
                } animate__animated animate__fadeIn`}
                disabled={loadingRequest}
                onClick={() => {
                  setModalAction(false);
                  cleanStates();
                }}
              >
                <MdArrowBack className="w-7 h-7" />
              </button>
            )}
            <div
              className={`text-blue-600 font-extrabold ${
                modalAction ? "text-2xl" : "text-xl"
              } px-2`}
            >
              {modalAction === "CREATE_POSITION"
                ? "Creating new position"
                : modalAction === "UPDATE_POSITION" &&
                  "Updating " + positionTitle}

              {/* POSITION STATUS CONTROLLER =============================== */}
              {modalAction !== "CREATE_POSITION" &&
                modalAction !== "UPDATE_POSITION" && (
                  <section className="flex items-start md:items-center justify-start md:justify-end gap-1 mt-1">
                    <span className="text-sm text-gray-600">Status: </span>
                    <div
                      className={`w-auto ${
                        props.systemPositions === null ||
                        loading ||
                        loadingRequest
                          ? "cursor-not-allowed"
                          : "cursor-pointer"
                      } ${
                        filterByPositionStatus === "TRUE"
                          ? "bg-blue-500 text-white font-bold"
                          : "bg-gray-100 text-gray-800"
                      } hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                      onClick={() =>
                        props.systemPositions !== null &&
                        !loading &&
                        !loadingRequest &&
                        setFilterByPositionStatus("TRUE")
                      }
                    >
                      <div className="flex-1 text-xs">
                        <MdBrightness1 className="inline-block mr-1 text-xs" />
                        <span>Active</span>
                      </div>
                    </div>
                    <div
                      className={`w-auto ${
                        props.systemPositions === null ||
                        loading ||
                        loadingRequest
                          ? "cursor-not-allowed"
                          : "cursor-pointer"
                      } ${
                        filterByPositionStatus === "FALSE"
                          ? "bg-blue-500 text-white font-bold"
                          : "bg-gray-100 text-gray-800"
                      } hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                      onClick={() =>
                        props.systemPositions !== null &&
                        !loading &&
                        !loadingRequest &&
                        setFilterByPositionStatus("FALSE")
                      }
                    >
                      <div className="flex-1 text-xs">
                        <MdBrightness1 className="inline-block mr-1 text-xs" />
                        <span>Deleted</span>
                      </div>
                    </div>
                  </section>
                )}
              {/* END POSITION STATUS CONTROLLER =============================== */}
            </div>
          </section>
          {!modalAction && props.systemPositions !== null && (
            <div
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
              onClick={() => {
                setModalAction("CREATE_POSITION");
                setFormSteps({
                  ONE: true,
                  TWO: false,
                  THREE: false,
                  FOUR: false,
                });
              }}
            >
              Add new
            </div>
          )}
        </div>
        {!modalAction ? (
          <div className="flex flex-col">
            {props.systemPositions &&
            filterByStatus(props.systemPositions, filterByPositionStatus)
              .length === 0 ? (
              <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
                <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
                <span>No position available</span>
              </div>
            ) : (
              <div>
                {/* FILTER FORM =================================== */}
                <section className="flex flex-col-reverse sm:flex-row items-center gap-2 w-full my-2">
                  <div className="w-full sm:w-3/4">
                    <input
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                        setSearchPosition(e.target.value)
                      }
                      type="text"
                      className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500 w-full"
                      placeholder={`Search position by title or description`}
                      value={searchPosition}
                      disabled={
                        filterByPublishement(
                          filterByStatus(
                            props.systemPositions,
                            filterByPositionStatus
                          ),
                          filterByPublishedStatus
                        ).length > 0
                          ? false
                          : true
                      }
                    />
                  </div>
                  {filterByPositionStatus === "TRUE" && (
                    <div className="w-full sm:w-1/4">
                      <select
                        className="input-white h-10 text-sm border-2 border-gray-400 focus:border-gray-500 w-full"
                        placeholder={`Filter by status`}
                        onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                          setFilterByPublishedStatus(
                            e.target.value as
                              | "ALL"
                              | "PUBLISHED"
                              | "NOT_PUBLISHED"
                          )
                        }
                        // value={searchPosition}
                      >
                        <option value="" disabled>
                          Filter by status
                        </option>
                        <option value="ALL">All positions</option>
                        <option value="PUBLISHED">Published positions</option>
                        <option value="NOT_PUBLISHED">
                          Not Published positions
                        </option>
                      </select>
                    </div>
                  )}
                </section>
                {/* END FILTER FORM =================================== */}
                {props.systemPositions &&
                searchData(
                  filterByPublishement(
                    filterByStatus(
                      props.systemPositions,
                      filterByPositionStatus
                    ),
                    filterByPublishedStatus
                  ),
                  searchPosition
                ).length > 0 ? (
                  searchData(
                    filterByPublishement(
                      filterByStatus(
                        props.systemPositions,
                        filterByPositionStatus
                      ),
                      filterByPublishedStatus
                    ),
                    searchPosition
                  ).map((item: PositionAPI, i: number) => (
                    <div
                      key={i + item.position_id}
                      className={`border ${
                        (!thisPosition ||
                          thisPosition.position_id !== item.position_id) &&
                        !item.isPublished
                          ? "border-yellow-500 hover:bg-yellow-100"
                          : "hover:bg-blue-50"
                      }`}
                    >
                      <div
                        className={`w-full flex flex-row px-3 py-2 rounded items-start my-1 justify-between group ${
                          styelingController(item).section
                        }`}
                      >
                        <div
                          className={`flex flex-col cursor-pointer`}
                          onClick={() => {
                            !loadingRequest &&
                            thisPosition &&
                            thisPosition.position_id === item.position_id
                              ? setThisPosition(null)
                              : setThisPosition(item);
                          }}
                        >
                          <span className="text-gray-600 font-bold">
                            {item.position_title}
                          </span>
                          <span className={`text-xs pr-5 text-gray-500`}>
                            {item.description}
                          </span>
                        </div>
                        <div className="flex flex-row items-center gap-2">
                          {(!thisPosition ||
                            thisPosition.position_id !== item.position_id) && (
                            <span
                              className={`inline-block rounded-full text-gray-800 duration-300 text-xs font-bold px-2 md:px-3 py-1 opacity-90 group-hover:opacity-100 ${
                                item.isPublished
                                  ? "bg-blue-50"
                                  : "bg-yellow-200"
                              }`}
                            >
                              {item.isPublished ? "Published" : "Unpublished"}
                            </span>
                          )}
                          {thisPosition &&
                            thisPosition.position_id === item.position_id && (
                              <div className="flex items-center gap-2">
                                {!item.isDeleted && (
                                  <button
                                    className={`${
                                      styelingController(item).button
                                    } rounded-full px-1 py-1 focus:outline-none ${
                                      loadingRequest
                                        ? "cursor-not-allowed"
                                        : "cursor-pointer"
                                    } animate__animated animate__fadeIn`}
                                    disabled={loadingRequest}
                                    onClick={() => {
                                      setThisEdication(
                                        item.education_institute_id
                                      );
                                      setThisLevel(item.school_level_id);
                                      setThisCourses(item.courses);
                                      setThisCoursesId(
                                        item.courses.map(
                                          (item) => item.course_id
                                        )
                                      );
                                      setPositionTitle(item.position_title);
                                      setPositionCode(item.position_code);
                                      setPositionId(item.position_id);
                                      setPositionDesc(item.description);
                                      setPositionSubCategoryId(
                                        item.subcategory_id
                                      );
                                      setThisRequirementList(
                                        assignRequirementsUpdate(
                                          item.requirements
                                        )
                                      );
                                      setFormSteps({
                                        ONE: true,
                                        TWO: false,
                                        THREE: false,
                                        FOUR: false,
                                      });
                                      setStepState({
                                        ONE: true,
                                        TWO: true,
                                        THREE: true,
                                        FOUR: true,
                                      });
                                      setModalAction("UPDATE_POSITION");
                                    }}
                                  >
                                    <MdEdit className="text-3xl text-blue-700" />
                                  </button>
                                )}
                                <button
                                  className={`${
                                    styelingController(item).button
                                  } rounded-full px-1 py-1 focus:outline-none ${
                                    loadingRequest || isDeleting
                                      ? "cursor-not-allowed"
                                      : "cursor-pointer"
                                  } animate__animated animate__fadeIn`}
                                  onClick={() =>
                                    setDeleteAction(item.position_id)
                                  }
                                  disabled={
                                    isDeleting || loadingRequest ? true : false
                                  }
                                >
                                  {item.isDeleted ? (
                                    <MdRestoreFromTrash className="text-3xl text-yellow-700" />
                                  ) : (
                                    <MdDelete className="text-3xl text-red-700" />
                                  )}
                                </button>
                              </div>
                            )}
                          <div
                            onClick={() => {
                              !loadingRequest &&
                              thisPosition &&
                              thisPosition.position_id === item.position_id
                                ? setThisPosition(null)
                                : setThisPosition(item);
                            }}
                            className={`${
                              thisPosition &&
                              thisPosition.position_id === item.position_id
                                ? styelingController(item).button
                                : !item.isPublished
                                ? "bg-gray-50 group-hover:bg-gray-100"
                                : "bg-gray-50 group-hover:bg-blue-100"
                            } rounded-full px-1 py-1 ${
                              loadingRequest
                                ? "cursor-not-allowed"
                                : "cursor-pointer"
                            }`}
                          >
                            {thisPosition &&
                            thisPosition.position_id === item.position_id ? (
                              <MdExpandLess className="text-3xl text-gray-700" />
                            ) : (
                              <MdExpandMore className="text-3xl text-gray-700" />
                            )}
                          </div>
                        </div>
                      </div>
                      {thisPosition &&
                        thisPosition.position_id === item.position_id && (
                          <ViewPosition
                            thisPosition={thisPosition}
                            setThisPosition={setThisPosition}
                          />
                        )}
                    </div>
                  ))
                ) : (
                  <NoResult
                    theme={NoResultThemes.Dark}
                    value={"No position found"}
                  />
                )}
              </div>
            )}
          </div>
        ) : (
          <PositionForm
            formSteps={formSteps}
            thisLevel={thisLevel}
            thisEdication={thisEdication}
            thisCoursesId={thisCoursesId}
            thisCourses={thisCourses}
            stepState={stepState}
            positionId={positionId}
            positionDesc={positionDesc}
            positionTitle={positionTitle}
            thisFieldList={thisFieldList}
            thisRequirementList={thisRequirementList}
            thisCategory={thisCategory}
            thisSubCategory={thisSubCategory}
            chooseFields={chooseFields}
            filteredFields={filteredFields}
            thisField={thisField}
            isFieldRequired={isFieldRequired}
            isRequirementRequired={isRequirementRequired}
            courseLength={courseLength}
            modalAction={modalAction}
            setThisEdication={setThisEdication}
            setThisLevel={setThisLevel}
            setFormSteps={setFormSteps}
            setThisCoursesId={setThisCoursesId}
            setThisCourses={setThisCourses}
            setStepState={setStepState}
            setPositionId={setPositionId}
            setModalAction={setModalAction}
            setThisFieldList={setThisFieldList}
            setPositionDesc={setPositionDesc}
            setPositionTitle={setPositionTitle}
            setThisCategory={setThisCategory}
            setThisSubCategory={setThisSubCategory}
            setThisRequirementList={setThisRequirementList}
            setFilteredFields={setFilteredFields}
            setThisField={setThisField}
            setIsFieldRequired={setIsFieldRequired}
            setIsRequirementRequired={setIsRequirementRequired}
            setCourseLength={setCourseLength}
            setChooseFields={setChooseFields}
            setThisPosition={setThisPosition}
            cleanStates={cleanStates}
            positionSubCategoryId={positionSubCategoryId}
            setPositionSubCategoryId={setPositionSubCategoryId}
            setPositionCode={setPositionCode}
            positionCode={positionCode}
          />
        )}
      </div>

      {!isEmptyOrSpaces(deleteAction) && thisPosition && (
        <ActionConfirm
          modalType={
            thisPosition.isDeleted
              ? ActionConfirmEnum.RESTORE_DELETE
              : ActionConfirmEnum.DELETE
          }
          confirmFun={() => handleDelete(deleteAction)}
          setModalAction={(status: boolean) => !status && setDeleteAction("")}
          description={`Do you really want to ${
            thisPosition.isDeleted
              ? "restore this position?"
              : "delete this position? This process can affect applications"
          }`}
        />
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
}: StoreState): {
  auth: Auth;
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    systemPositions: system_settings.systemPositions,
  };
};

export const Positions = connect(mapStateToProps, {
  getPositions,
  deletePosition,
  PushPopupAlert,
})(Index);
