import React, { Fragment, useState } from "react";
import { MdCheckBox, MdCheckBoxOutlineBlank } from "react-icons/md";
import { positionRequirementInterface } from ".";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import {
  DocumentAttributesInterface,
  DocumentCategoryInterface,
  DocumentFieldsInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import FieldsForm from "./FieldsForm";

interface AddRequirementsProps {
  thisCategory: string;
  documentFields: DocumentFieldsInterface[] | null;
  documentCategory: DocumentCategoryInterface[] | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  thisSubCategory: string;
  documentSubCategory: DocumentSubCategoryInterface[] | null;
  chooseFields: boolean;
  filteredFields: DocumentFieldsInterface[] | null;
  loading: boolean;
  isFieldRequired: boolean;
  thisFieldList: positionRequirementInterface[] | null;
  setAddNewRequirement: React.Dispatch<React.SetStateAction<boolean>>;
  setThisCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisSubCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisFieldList: React.Dispatch<
    React.SetStateAction<positionRequirementInterface[] | null>
  >;
  setThisRequirementList: React.Dispatch<
    React.SetStateAction<
      | {
          user_detail_category_id: string;
          user_detail_subcategory_id: string;
          position_requirement: positionRequirementInterface[];
          required: boolean;
        }[]
      | null
    >
  >;
  setFilteredFields: React.Dispatch<
    React.SetStateAction<DocumentFieldsInterface[] | null>
  >;
  setChooseFields: React.Dispatch<React.SetStateAction<boolean>>;
  setThisField: React.Dispatch<React.SetStateAction<string>>;
  setIsFieldRequired: React.Dispatch<React.SetStateAction<boolean>>;
  setFormError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    } | null>
  >;
  setIsRequirementRequired: React.Dispatch<React.SetStateAction<boolean>>;
  isRequirementRequired: boolean;
  thisRequirementList:
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null;
  positionSubCategoryId: string;
  setPositionSubCategoryId: React.Dispatch<React.SetStateAction<string>>;
  hasSubCategory: (_ID: string) => boolean;
}

const AddRequirements: React.FC<AddRequirementsProps> = (props) => {
  const [isAnyDocument, setIsAnyDocument] = useState<boolean>(true);

  const filterFields = (TYPE: "CATEGORY" | "SUB_CATEGORY", VALUE: string) => {
    let newArray: DocumentFieldsInterface[] = props.documentFields
      ? props.documentFields
      : [];
    if (TYPE === "CATEGORY") {
      newArray = newArray.filter((item) => item.category_id === VALUE);
    } else if (TYPE === "SUB_CATEGORY") {
      newArray = newArray.filter(
        (item) =>
          item.category_id === props.thisCategory &&
          (item.subcategory_id === VALUE || item.subcategory_id === null)
      );
    } else {
      newArray = [];
    }
    props.setFilteredFields(newArray);
  };

  const getExistFields = (
    TYPE: "CATEGORY" | "SUB_CATEGORY",
    VALUE: string
  ): positionRequirementInterface[] => {
    if (TYPE === "CATEGORY") {
      if (
        props.thisRequirementList &&
        props.thisRequirementList.length > 0 &&
        !props.hasSubCategory(VALUE)
      ) {
        let data = props.thisRequirementList.filter(
          (item) => item.user_detail_category_id === VALUE
        );
        if (data.length > 0) {
          return data[0].position_requirement;
        }
      }
    } else if (TYPE === "SUB_CATEGORY") {
      if (props.thisRequirementList && props.thisRequirementList.length > 0) {
        let data = props.thisRequirementList.filter(
          (item) =>
            item.user_detail_category_id === props.thisCategory &&
            (item.user_detail_subcategory_id === VALUE ||
              item.user_detail_subcategory_id === null)
        );
        if (data.length > 0) {
          return data[0].position_requirement;
        }
      }
    }
    return [];
  };

  const getCategoryById = (
    category_id: string
  ): DocumentCategoryInterface | null => {
    let result: DocumentCategoryInterface | undefined;
    if (props.documentCategory) {
      result = props.documentCategory.find(
        (item) => item.user_detail_category_id === category_id
      );
    }

    return result ? result : null;
  };

  const addFieldRequirements = (
    thisField: positionRequirementInterface,
    required: boolean = false
  ) => {
    let requireStatus: boolean = thisField.required;

    if (props.thisFieldList) {
      let index: number = props.thisFieldList.findIndex(
        (field) =>
          field.category_subcategory_data_id ===
          thisField.category_subcategory_data_id
      );
      if (index >= 0) {
        let newArray = [...props.thisFieldList];
        if (required) {
          requireStatus = !newArray[index].required;
          newArray[index] = {
            category_subcategory_data_id:
              thisField.category_subcategory_data_id,
            required: requireStatus,
          };
        } else {
          newArray.splice(index, 1);
        }
        if (newArray.length > 0) {
          props.setThisFieldList(newArray);
          setIsAnyDocument(false);
        } else {
          props.setThisFieldList(null);
          setIsAnyDocument(true);
        }
      } else {
        props.setThisFieldList([
          ...props.thisFieldList,
          {
            category_subcategory_data_id:
              thisField.category_subcategory_data_id,
            required: thisField.required,
          },
        ]);
        setIsAnyDocument(false);
      }
      if (props.formError && props.formError.element === "position_field") {
        props.setFormError(null);
      }
    } else {
      props.setThisFieldList([
        {
          category_subcategory_data_id: thisField.category_subcategory_data_id,
          required: thisField.required,
        },
      ]);
      setIsAnyDocument(false);
      if (props.formError && props.formError.element === "position_field") {
        props.setFormError(null);
      }
    }
  };

  const addRequirement = () => {
    if (!props.thisCategory) {
      return props.setFormError({
        element: "position_cat",
        msg: "Document category is required",
      });
    }

    if (isEmptyOrSpaces(props.thisSubCategory)) {
      return props.setFormError({
        element: "position_sub",
        msg: "Qualification is required!",
      });
    }

    // if (!props.thisFieldList || !props.thisFieldList.length) {
    //   return props.setFormError({
    //     element: "position_field",
    //     msg: "Document field is required",
    //   });
    // }

    if (props.thisRequirementList) {
      let index: number = props.thisRequirementList.findIndex(
        (item) => item.user_detail_category_id === props.thisCategory
      );
      if (props.hasSubCategory(props.thisCategory)) {
        index = props.thisRequirementList.findIndex(
          (item) =>
            item.user_detail_category_id === props.thisCategory &&
            item.user_detail_subcategory_id === props.thisSubCategory
        );
      }

      if (index >= 0) {
        let newArray = [...props.thisRequirementList];
        if (props.hasSubCategory(props.thisCategory)) {
          let index_2: number = props.thisRequirementList.findIndex(
            (item) => item.user_detail_subcategory_id === props.thisSubCategory
          );
          if (index_2 >= 0) {
            newArray[index].user_detail_category_id = props.thisCategory;
            newArray[index].user_detail_subcategory_id = props.thisSubCategory
              ? props.thisSubCategory
              : "";
            newArray[index].position_requirement = props.thisFieldList
              ? props.thisFieldList
              : [];
            newArray[index].required = props.isRequirementRequired;
            props.setThisRequirementList(newArray);
          } else {
            props.setThisRequirementList([
              ...props.thisRequirementList,
              {
                user_detail_category_id: props.thisCategory,
                user_detail_subcategory_id: props.thisSubCategory
                  ? props.thisSubCategory
                  : "",
                position_requirement: props.thisFieldList
                  ? props.thisFieldList
                  : [],
                required: props.isRequirementRequired,
              },
            ]);
          }
        } else {
          newArray[index].user_detail_category_id = props.thisCategory;
          newArray[index].user_detail_subcategory_id = props.thisSubCategory
            ? props.thisSubCategory
            : "";
          newArray[index].position_requirement = props.thisFieldList
            ? props.thisFieldList
            : [];
          newArray[index].required = props.isRequirementRequired;
          props.setThisRequirementList(newArray);
        }
      } else {
        props.setThisRequirementList([
          ...props.thisRequirementList,
          {
            user_detail_category_id: props.thisCategory,
            user_detail_subcategory_id: props.thisSubCategory
              ? props.thisSubCategory
              : "",
            position_requirement: props.thisFieldList
              ? props.thisFieldList
              : [],
            required: props.isRequirementRequired,
          },
        ]);
      }
    } else {
      props.setThisRequirementList([
        {
          user_detail_category_id: props.thisCategory,
          user_detail_subcategory_id: props.thisSubCategory
            ? props.thisSubCategory
            : "",
          position_requirement: props.thisFieldList ? props.thisFieldList : [],
          required: props.isRequirementRequired,
        },
      ]);
    }

    props.setThisCategory("");
    props.setThisSubCategory("");
    props.setThisFieldList([]);
    props.setThisField("");
    props.setIsRequirementRequired(false);
    props.setIsFieldRequired(false);
    props.setAddNewRequirement(false);
  };

  const categoryChoosen = (category_id: string) => {
    let _category = getCategoryById(category_id);
    if (_category) {
      props.setThisCategory(category_id);
      props.setThisSubCategory("");
      if (
        _category.fields.includes(
          DocumentAttributesInterface.category_subcategory_data
        )
      ) {
        if (!props.hasSubCategory(category_id)) {
          console.log({ ___: !props.hasSubCategory(category_id) });
          console.log({ _category: _category.fields });
          filterFields("CATEGORY", category_id);
          props.setThisFieldList(getExistFields("CATEGORY", category_id));
          if (props.thisRequirementList) {
            let isRequired = props.thisRequirementList.filter(
              (item) => item.user_detail_category_id === category_id
            );
            isRequired
              ? props.setIsRequirementRequired(isRequired[0].required)
              : props.setIsRequirementRequired(false);
          }
        } else {
          props.setThisFieldList(null);
          props.setFilteredFields(null);
        }
      } else {
        props.setThisFieldList(null);
        props.setFilteredFields(null);
      }
    }
  };

  return (
    <Fragment>
      {/* MODAL CONTROLLER */}

      <Modal
        title={
          <div className="flex flex-row gap-4 items-center">
            <div className="flex flex-col">Adding New Requirement</div>
          </div>
        }
        backDrop={true}
        theme={Themes.default}
        close={() => {
          props.setAddNewRequirement(false);
          props.setThisCategory("");
          props.setThisSubCategory("");
          props.setFormError(null);

          // cleanStates();
        }}
        widthSizeClass={ModalSize.large}
        displayClose={true}
        backDropClose={false}
        padding={{ title: true, body: false }}
      >
        <div className="px-4">
          {/* ==================== Position requirements ======================== */}
          <div className="p-3 mb-3">
            <span className="text-xl text-gray-600 font-bold mb-1 hidden">
              Position requirements
            </span>
            {/* DOCUMENT CATEGORY */}
            <div className="flex flex-col mb-2">
              <div className="text-sm text-gray-600 font-bold mb-1">
                Document category
              </div>
              <select
                className={`block w-full bg-white text-grey-darker border-2 rounded h-10 px-3 focus:outline-none ${
                  props.formError !== null &&
                  props.formError.element === "position_cat"
                    ? "border-red-500"
                    : "border-gray-500"
                }`}
                onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                  categoryChoosen(e.target.value);
                }}
                value={props.thisCategory ? props.thisCategory : ""}
              >
                <option value="" disabled>
                  Choose Document Category
                </option>
                {props.documentCategory && props.documentCategory.length > 0 ? (
                  props.documentCategory.map((item, index) => (
                    <option
                      value={item.user_detail_category_id}
                      key={index + item.user_detail_category_id}
                    >
                      {item.title}
                    </option>
                  ))
                ) : (
                  <option value="">No Document Category found</option>
                )}
              </select>
              {props.formError !== null &&
                props.formError.element === "position_cat" && (
                  <span className="text-red-600 font-bold text-xs">
                    {props.formError.msg}
                  </span>
                )}
            </div>
            {/* DOCUMENT SUB CATEGORY */}
            {props.thisCategory.length > 0 && (
              <div className="flex flex-col mb-2 animate__animated animate__fadeIn">
                <div className="text-sm text-gray-600 font-bold mb-1">
                  Qualification of{" "}
                  {getCategoryById(props.thisCategory)
                    ? getCategoryById(props.thisCategory)?.title
                    : "Category"}
                </div>
                <select
                  className={`block w-full bg-white text-grey-darker border-2 rounded h-10 px-3 focus:outline-none ${
                    props.formError !== null &&
                    props.formError.element === "position_sub"
                      ? "border-red-500"
                      : "border-gray-500"
                  }`}
                  onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                    props.setThisSubCategory(e.target.value);
                    filterFields("SUB_CATEGORY", e.target.value);
                    props.setThisFieldList(
                      getExistFields("SUB_CATEGORY", e.target.value)
                    );
                    setIsAnyDocument(
                      getExistFields("SUB_CATEGORY", e.target.value).length
                        ? false
                        : true
                    );
                    isEmptyOrSpaces(props.positionSubCategoryId) &&
                      props.setPositionSubCategoryId(e.target.value);
                    props.setIsRequirementRequired(true);
                    props.formError !== null &&
                      props.formError.element === "position_sub" &&
                      props.setFormError(null);
                  }}
                  value={props.thisSubCategory ? props.thisSubCategory : ""}
                >
                  <option value="" disabled>
                    Choose Qualification
                  </option>
                  {props.documentSubCategory &&
                  props.documentSubCategory.filter(
                    (item) => item.category_id === props.thisCategory
                  ).length > 0 ? (
                    props.documentSubCategory
                      .filter((item) => item.category_id === props.thisCategory)
                      .map((item, index) => (
                        <option
                          value={item.user_detail_subcategory_id}
                          key={index + item.user_detail_subcategory_id}
                        >
                          {item.title}
                        </option>
                      ))
                  ) : (
                    <option value="">No qualification found</option>
                  )}
                </select>
                {props.formError !== null &&
                  props.formError.element === "position_sub" && (
                    <span className="text-red-600 font-bold text-xs">
                      {props.formError.msg}
                    </span>
                  )}
              </div>
            )}
            {/* Qualification is required or not //////////////////// */}
            {!isEmptyOrSpaces(props.thisSubCategory) && (
              <div
                className="flex items-center text-lg gap-1 mt-2 animate__animated animate__fadeIn cursor-pointer"
                onClick={() =>
                  !props.loading &&
                  props.setIsRequirementRequired(!props.isRequirementRequired)
                }
              >
                <div className="text-gray-600 font-bold">
                  Qualification is compulsory
                </div>
                {props.isRequirementRequired ? (
                  <MdCheckBox className="w-6 h-6 text-blue-700" />
                ) : (
                  <MdCheckBoxOutlineBlank className="w-6 h-6" />
                )}
              </div>
            )}
            {/* DOCUMENT FIELD */}
            {props.thisCategory.length > 0 && (
              <div className="flex flex-col mb-2 animate__animated animate__fadeIn">
                <div className="flex justify-between mt-2">
                  <div className="text-sm text-gray-600 font-bold mb-1">
                    Documents
                  </div>
                  {!isEmptyOrSpaces(props.thisSubCategory) && (
                    <div
                      className="flex items-center text-lg gap-1 mb-1 animate__animated animate__fadeIn cursor-pointer"
                      onClick={() =>
                        !props.loading &&
                        props.setIsRequirementRequired(
                          !props.isRequirementRequired
                        )
                      }
                    >
                      <div className="text-sm text-gray-600 font-bold mb-1">
                        Any document
                      </div>
                      {isAnyDocument ? (
                        <MdCheckBox className="w-6 h-6 text-blue-700" />
                      ) : (
                        <MdCheckBoxOutlineBlank className="w-6 h-6" />
                      )}
                    </div>
                  )}
                </div>
                <div
                  className={`input-white border-2 ${
                    !props.filteredFields && "h-10 cursor-pointer"
                  } px-3 max-h-96 overflow-y-auto text-gray-800 ${
                    props.formError !== null &&
                    props.formError.element === "position_field"
                      ? "border-red-500"
                      : "border-gray-500"
                  }`}
                >
                  {props.filteredFields ? (
                    <FieldsForm
                      filteredFields={props.filteredFields}
                      isFieldRequired={props.isFieldRequired}
                      thisFieldList={props.thisFieldList}
                      setThisField={props.setThisField}
                      setIsFieldRequired={props.setIsFieldRequired}
                      setFormError={props.setFormError}
                      setChooseFields={props.setChooseFields}
                      addFieldRequirements={addFieldRequirements}
                    />
                  ) : (
                    ""
                  )}
                </div>
                {props.formError !== null &&
                  props.formError.element === "position_field" && (
                    <span className="text-red-600 font-bold text-xs">
                      {props.formError.msg}
                    </span>
                  )}
              </div>
            )}
            {/* DOCUMENT REQUIREMENT FOOTER */}
            {props.thisCategory.length > 0 && (
              <div className="flex items-center justify-between">
                <button
                  className="btn bg-gray-500 text-gray-50 hover:bg-gray-700 mb-2 animate__animated animate__fadeIn focus:outline-none"
                  onClick={() => {
                    props.setAddNewRequirement(false);
                    props.setThisCategory("");
                    props.setThisSubCategory("");
                    props.setFormError(null);
                  }}
                >
                  Cancel
                </button>
                <button
                  className="btn btn-blue mb-2 animate__animated animate__fadeIn focus:outline-none"
                  onClick={() => addRequirement()}
                >
                  Add
                </button>
              </div>
            )}
          </div>
        </div>
      </Modal>
      {/* MODAL CONTROLLER */}
    </Fragment>
  );
};

export default React.memo(AddRequirements);
