import React, { useState } from "react";
import { HiOutlineDocumentSearch } from "react-icons/hi";
import { schoolLevelsStoreInterface } from "../../actions";
import { course } from "../../interfaces/course";
import { education_category } from "../../interfaces/education_category";
import { school_level } from "../../interfaces/school_level";

type SchoolLevelsFormPropsInterface = {
  schoolLevels: schoolLevelsStoreInterface;
  thisEdication: education_category;
  loading: boolean;
  thisLevel: school_level | null;
  setThisLevel: React.Dispatch<React.SetStateAction<school_level | null>>;
  setThisCourses: React.Dispatch<React.SetStateAction<course[] | null>>;
};

const SchooLevelsForm: React.FC<SchoolLevelsFormPropsInterface> = (props) => {
  const [thisLevelId, setThisLevel] = useState<string>(
    props.thisLevel ? props.thisLevel.school_level_id : ""
  );

  const FC_SetLevel = (level: school_level) => {
    props.setThisLevel(level);
    props.setThisCourses(null);
  };

  const FC_SetLevelBySelect = (levelId: string) => {
    if (props.schoolLevels.school_levels && props.thisEdication) {
      const thisEdication = props.thisEdication;
      const level = props.schoolLevels.school_levels
        .filter((item) =>
          item.education_institute.includes(
            thisEdication.education_institute_id
          )
        )
        .find((item) => item.school_level_id === levelId);
      if (level) {
        props.thisLevel
          ? props.thisLevel.school_level_id !== level.school_level_id
            ? FC_SetLevel(level)
            : props.setThisLevel(level)
          : props.setThisLevel(level);
      } else {
        props.setThisLevel(null);
      }
    }
  };

  return (
    <section className="m-0">
      <div className="text-lg ml-1 font-semibold text-gray-600">
        School level:
      </div>

      {props.schoolLevels.school_levels &&
      props.schoolLevels.school_levels.filter((item) =>
        item.education_institute.includes(
          props.thisEdication.education_institute_id
        )
      ).length > 0 ? (
        <select
          className={`px-3 py-3 w-full m-1 border border-gray-700 rounded bg-white hover:bg-blue-50 text-lg focus:outline-none`}
          value={thisLevelId}
          disabled={props.loading}
          onChange={(e) => {
            FC_SetLevelBySelect(e.target.value);
            setThisLevel(e.target.value);
          }}
        >
          <option value="">Choose School level</option>
          {props.schoolLevels.school_levels
            .filter((item) =>
              item.education_institute.includes(
                props.thisEdication!.education_institute_id
              )
            )
            .map((item, index) => (
              <option
                key={index + item.school_level_id}
                value={item.school_level_id}
              >
                {item.school_level_name}
              </option>
            ))}
        </select>
      ) : (
        <div className="text-gray-400 px-6 py-4 rounded border mt-4 text-xl font-extrabold flex flex-col items-center justify-center bg-gray-100">
          <HiOutlineDocumentSearch className="text-7xl text-gray-400 animate__animated animate__shakeX" />
          <span>No School level available</span>
        </div>
      )}
    </section>
  );
};

export default React.memo(SchooLevelsForm);
