export const GetDistinctQualification = (
  qualifications: DocumentSubCategoryInterface[],
  RequirementData: {
    user_detail_category_id: string;
    user_detail_subcategory_id: string;
    position_requirement: positionRequirementInterface[];
    required: boolean;
  }[]
): DocumentSubCategoryInterface[] => {
  let result: DocumentSubCategoryInterface[] = [];
  let ids: string[] = [];
  RequirementData.forEach((requirement) => {
    if (!ids.includes(requirement.user_detail_subcategory_id)) {
      const data: DocumentSubCategoryInterface | undefined =
        qualifications.find(
          (item) =>
            item.user_detail_subcategory_id ===
            requirement.user_detail_subcategory_id
        );
      data && result.push(data);
      data && ids.push(requirement.user_detail_subcategory_id);
    }
  });
  qualifications.forEach((qualification) => {
    if (
      !ids.includes(qualification.user_detail_subcategory_id) &&
      RequirementData.find(
        (item) =>
          item.user_detail_subcategory_id ===
          qualification.user_detail_subcategory_id
      )
    ) {
      result.push(qualification);
      ids.push(qualification.user_detail_subcategory_id);
    }
  });
  return result;
};

