import React, { Fragment } from "react";
import { MdCheckBox, MdCheckBoxOutlineBlank } from "react-icons/md";
import { positionRequirementInterface } from ".";
import Table from "../../components/Table/Table";
import { DocumentFieldsInterface } from "../../shared/interfaces";

interface FieldsFormProps {
  filteredFields: DocumentFieldsInterface[];
  isFieldRequired: boolean;
  thisFieldList: positionRequirementInterface[] | null;
  setThisField: React.Dispatch<React.SetStateAction<string>>;
  setIsFieldRequired: React.Dispatch<React.SetStateAction<boolean>>;
  setFormError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    } | null>
  >;
  setChooseFields: React.Dispatch<React.SetStateAction<boolean>>;
  addFieldRequirements: (
    thisField: positionRequirementInterface,
    required?: boolean
  ) => void;
}

const FieldsForm: React.FC<FieldsFormProps> = (props) => {
  const tableData = (): JSX.Element[] => {
    if (!props.filteredFields.length) {
      return [
        <tr key={4323433} className="border-b border-gray-200">
          <td className="p-3 text-center whitespace-nowrap" colSpan={2}>
            No Field Found
          </td>
        </tr>,
      ];
    }

    let all_fields: JSX.Element[] = props.filteredFields.map((field, index) => (
      <tr
        key={field._id + index}
        className="border-b border-gray-200 hover:bg-gray-100 cursor-pointer"
      >
        <td className="px-3 text-left">
          <div
            className="flex items-center gap-1"
            onClick={() =>
              props.addFieldRequirements({
                category_subcategory_data_id: field._id,
                required: true,
              })
            }
          >
            {props.thisFieldList &&
            props.thisFieldList.find(
              (item) => item.category_subcategory_data_id === field._id
            ) ? (
              <MdCheckBox className="w-6 h-6 text-blue-700" />
            ) : (
              <MdCheckBoxOutlineBlank className="w-6 h-6" />
            )}
            <span>{field.title}</span>
          </div>
        </td>
        <td className="px-5 text-right">
          <div
            className="flex flex-row items-center float-right text-lg gap-1 animate__animated animate__fadeIn cursor-pointer"
            onClick={() =>
              props.addFieldRequirements(
                {
                  category_subcategory_data_id: field._id,
                  required: true,
                },
                true
              )
            }
          >
            <div className="text-gray-600 font-bold">Required</div>
            {props.thisFieldList &&
            props.thisFieldList.find(
              (item) =>
                item.category_subcategory_data_id === field._id &&
                item.required === true
            ) ? (
              <MdCheckBox className="w-6 h-6 text-blue-700" />
            ) : (
              <MdCheckBoxOutlineBlank className="w-6 h-6" />
            )}
          </div>
        </td>
      </tr>
    ));

    // const footer: JSX.Element = (
    //   <tr key={43200033} className="border-b border-gray-200">
    //     <td className="p-3 whitespace-nowrap text-right" colSpan={2}>
    //       <button
    //         className="btn btn-blue"
    //         onClick={() => {
    //           props.setChooseFields(false);
    //         }}
    //         // disabled={loadingRequest}
    //       >
    //         Close
    //       </button>
    //     </td>
    //   </tr>
    // );
    // all_fields.push(footer);

    return all_fields;
  };
  return (
    <Fragment>
      <Table
        columns={[
          { columnTitle: "Names", alignText: "left", padding: "p-3" },
          { columnTitle: "", alignText: "left", padding: "p-3" },
        ]}
        rows={tableData}
        pagination={false}
        search={true}
        hideColumns={true}
        thColor="gray"
      />
    </Fragment>
  );
};

export default React.memo(FieldsForm);
