import React, { Fragment } from "react";
import { positionRequirementInterface } from ".";
import { course } from "../../interfaces/course";
import { education_category } from "../../interfaces/education_category";
import { PositionAPI } from "../../interfaces/position";
import { school_level } from "../../interfaces/school_level";
import { DocumentFieldsInterface } from "../../shared/interfaces";
import StepOne from "./StepOne";
import StepsHeader from "./StepsHeader";
import StepsReview from "./StepsReview";
import StepThree from "./StepThree";
import StepTwo from "./StepTwo";

interface PositionFormProps {
  formSteps: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
    FOUR: boolean;
  };
  positionId: string;
  positionDesc: string;
  positionTitle: string;
  positionCode: string;
  thisLevel: school_level | null;
  positionSubCategoryId: string;
  thisEdication: education_category | null;
  thisCoursesId: string[] | null;
  thisCourses: course[] | null;
  thisFieldList: positionRequirementInterface[] | null;
  thisRequirementList:
    | {
        user_detail_category_id: string;
        user_detail_subcategory_id: string;
        position_requirement: positionRequirementInterface[];
        required: boolean;
      }[]
    | null;
  thisCategory: string;
  stepState: {
    ONE: boolean;
    TWO: boolean;
    THREE: boolean;
    FOUR: boolean;
  };
  thisSubCategory: string;
  chooseFields: boolean;
  filteredFields: DocumentFieldsInterface[] | null;
  thisField: string;
  isFieldRequired: boolean;
  isRequirementRequired: boolean;
  courseLength: number;
  modalAction: false | "CREATE_POSITION" | "UPDATE_POSITION";

  setPositionId: React.Dispatch<React.SetStateAction<string>>;
  setThisEdication: React.Dispatch<
    React.SetStateAction<education_category | null>
  >;
  setThisLevel: React.Dispatch<React.SetStateAction<school_level | null>>;
  setFormSteps: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  setThisCoursesId: React.Dispatch<React.SetStateAction<string[] | null>>;
  setThisCourses: React.Dispatch<React.SetStateAction<course[] | null>>;
  setStepState: React.Dispatch<
    React.SetStateAction<{
      ONE: boolean;
      TWO: boolean;
      THREE: boolean;
      FOUR: boolean;
    }>
  >;
  setModalAction: React.Dispatch<
    React.SetStateAction<false | "CREATE_POSITION" | "UPDATE_POSITION">
  >;
  setThisFieldList: React.Dispatch<
    React.SetStateAction<positionRequirementInterface[] | null>
  >;
  setPositionDesc: React.Dispatch<React.SetStateAction<string>>;
  setPositionTitle: React.Dispatch<React.SetStateAction<string>>;
  setPositionCode: (value: string) => void;
  setThisCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisSubCategory: React.Dispatch<React.SetStateAction<string>>;
  setThisRequirementList: React.Dispatch<
    React.SetStateAction<
      | {
          user_detail_category_id: string;
          user_detail_subcategory_id: string;
          position_requirement: positionRequirementInterface[];
          required: boolean;
        }[]
      | null
    >
  >;
  setFilteredFields: React.Dispatch<
    React.SetStateAction<DocumentFieldsInterface[] | null>
  >;
  setThisField: React.Dispatch<React.SetStateAction<string>>;
  setIsFieldRequired: React.Dispatch<React.SetStateAction<boolean>>;
  setIsRequirementRequired: React.Dispatch<React.SetStateAction<boolean>>;
  setCourseLength: React.Dispatch<React.SetStateAction<number>>;
  setChooseFields: React.Dispatch<React.SetStateAction<boolean>>;
  setThisPosition: React.Dispatch<React.SetStateAction<PositionAPI | null>>;
  setPositionSubCategoryId: React.Dispatch<React.SetStateAction<string>>;
  cleanStates: () => void;
}

const PositionForm: React.FC<PositionFormProps> = (props) => {
  // const isNextAllowed=()=>{}

  return (
    <Fragment>
      <StepsHeader
        currentStep={
          props.formSteps.FOUR
            ? 4
            : props.formSteps.THREE
            ? 3
            : props.formSteps.TWO
            ? 2
            : 1
        }
        thisLevel={props.thisLevel}
        thisEdication={props.thisEdication}
        thisCourses={props.thisCourses}
        stepState={props.stepState}
        setFormSteps={props.setFormSteps}
      />

      {props.formSteps.ONE && (
        <StepOne
          thisLevel={props.thisLevel}
          thisEdication={props.thisEdication}
          formSteps={props.formSteps}
          setThisEdication={props.setThisEdication}
          setThisLevel={props.setThisLevel}
          setFormSteps={props.setFormSteps}
          setThisCourses={props.setThisCourses}
          setStepState={props.setStepState}
          modalAction={props.modalAction}
        />
      )}

      {props.formSteps.TWO && props.thisLevel && (
        <StepTwo
          thisLevel={props.thisLevel}
          thisCourses={props.thisCourses}
          thisCoursesId={props.thisCoursesId}
          formSteps={props.formSteps}
          setThisCourses={props.setThisCourses}
          setThisCoursesId={props.setThisCoursesId}
          setFormSteps={props.setFormSteps}
          setStepState={props.setStepState}
          modalAction={props.modalAction}
        />
      )}

      {props.formSteps.THREE &&
        props.thisEdication &&
        props.thisLevel &&
        props.thisCoursesId &&
        props.thisCourses && (
          <StepThree
            positionId={props.positionId}
            positionDesc={props.positionDesc}
            positionTitle={props.positionTitle}
            thisFieldList={props.thisFieldList}
            thisRequirementList={props.thisRequirementList}
            thisEdication={props.thisEdication}
            thisLevel={props.thisLevel}
            thisCategory={props.thisCategory}
            thisSubCategory={props.thisSubCategory}
            thisCoursesId={props.thisCoursesId}
            thisCourses={props.thisCourses}
            chooseFields={props.chooseFields}
            filteredFields={props.filteredFields}
            thisField={props.thisField}
            isFieldRequired={props.isFieldRequired}
            isRequirementRequired={props.isRequirementRequired}
            courseLength={props.courseLength}
            formSteps={props.formSteps}
            modalAction={props.modalAction}
            setPositionId={props.setPositionId}
            setModalAction={props.setModalAction}
            setThisFieldList={props.setThisFieldList}
            setPositionDesc={props.setPositionDesc}
            setPositionTitle={props.setPositionTitle}
            setThisCategory={props.setThisCategory}
            setThisSubCategory={props.setThisSubCategory}
            setThisRequirementList={props.setThisRequirementList}
            setFormSteps={props.setFormSteps}
            setFilteredFields={props.setFilteredFields}
            setThisField={props.setThisField}
            setIsFieldRequired={props.setIsFieldRequired}
            setIsRequirementRequired={props.setIsRequirementRequired}
            setCourseLength={props.setCourseLength}
            setChooseFields={props.setChooseFields}
            cleanStates={props.cleanStates}
            setStepState={props.setStepState}
            positionSubCategoryId={props.positionSubCategoryId}
            setPositionSubCategoryId={props.setPositionSubCategoryId}
            positionCode={props.positionCode}
            setPositionCode={props.setPositionCode}
          />
        )}
      {props.formSteps.FOUR &&
        props.thisEdication &&
        props.thisLevel &&
        props.thisCoursesId &&
        props.thisCourses &&
        props.thisRequirementList && (
          <StepsReview
            modalAction={props.modalAction}
            positionId={props.positionId}
            positionTitle={props.positionTitle}
            thisCourses={props.thisCourses}
            positionDesc={props.positionDesc}
            thisLevel={props.thisLevel}
            thisEdication={props.thisEdication}
            thisCoursesId={props.thisCoursesId}
            thisRequirementList={props.thisRequirementList}
            formSteps={props.formSteps}
            setFormSteps={props.setFormSteps}
            setModalAction={props.setModalAction}
            setThisPosition={props.setThisPosition}
            cleanStates={props.cleanStates}
            positionSubCategoryId={props.positionSubCategoryId}
            positionCode={props.positionCode}
          />
        )}
    </Fragment>
  );
};

export default React.memo(PositionForm);
